import React, { Component } from 'react';
import Message from './message';
import '../styles/chat.css';

class Chat extends Component {
    render() {
        return (
            <ul className="chat">
                {this.props.chat.map((message, i) => <Message value={message.value} sender={message.sender} key={i}/>)}
            </ul>
        );
    }
}
 
export default Chat;